<template>
	<view class="background fix-padding">
		<!-- 搜索 -->
		<view class="container search">
			<view class="flex">
				<uni-easyinput style="width: 300rpx;" class="input" v-model="apply_name	" placeholder="请输入供应商"></uni-easyinput>
				<uni-easyinput style="width: 300rpx;" class="input" v-model="order_no" placeholder="请输入单号"></uni-easyinput>
				<button class="btn-search" @click="search">查询</button>
			</view>
			<view class="flex mt-20">
				<view class="text">自定义查</view>
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="start_time" type="date" placeholder="开始日期"/>
				至
				<uni-datetime-picker class="date-picker" ref="formDate" v-model="end_time" type="date" placeholder="结束日期"/>
				<button class="btn-search btn-color" @click="searchTime">查询</button>
			</view>
		</view>
		<!-- 列表 -->
		<view class="container list">
			<view class="flex flex_between total_top">
				<view class="flex">
					<view class="total_top_one">应付款总额</view>
					<view class="total_top_two">￥{{all_money}}</view>
				</view>
				<view  class="total_top_line"></view>
				<view class="flex">
					<view  class="total_top_one">已付款总额</view>
					<view  class="total_top_two">￥{{balance_money}}</view>
				</view>
			</view>
			<view class="item" v-for="(item,index) in list" :key="item.order_id">
				<view class="flex top .mb-10">
					<view class="title grey">采购单号</view>
					<view class="value">{{item.order_no}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">供应商</view>
					<view class="value">{{item.apply_name}}</view>
				</view>
				<view class="flex .mb-10">
					<view class="title grey">应付总额</view>
					<view class="value">{{item.total_money}}</view>
				</view>
				<view class="flex .mb-10 last">
					<view class="title grey">已付总额</view>
					<view class="value">￥{{item.balance_money}}</view>
				</view>
				<view class="flex .mb-10 last">
					<view class="title grey">待付总额</view>
					<view class="value">￥{{item.remain_money}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">联系人</view>
					<view class="value">{{item.contacts}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">数量</view>
					<view class="value">{{item.total_count}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">日期</view>
					<view class="value">{{item.date}}</view>
				</view>
				<view class="flex top mb-10">
					<view class="title grey">业务员</view>
					<view class="value">{{item.user_name}}</view>
				</view>
				<view class="arrow flex flex-column" @click="detail(item.order_id)">
					<view>详情</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				start_time:'',
				end_time:'',
				apply_name:'',
				order_no:'',
				date:'',
				list:[],
				page:0,
				all_money:0,
				balance_money:0,
			}
		},
		onLoad() {
			this.page = 0
			// 出库记录接口
			this.listInterface()
		},
		//上拉触底函数
		onReachBottom(){
			// 列表接口
			this.http({
				url: this.api.work_purchase_list,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					apply_name:this.apply_name,
					order_no:this.order_no,
					page:this.page
				},
				success: res => {
					this.page+=1
					if(res.list.length == 0) {
						uni.showToast({
							title:'没有更多了',
							icon:'none',
							duration: 2000
						})
					} else {
						this.list = this.list.concat(res.list)
						this.balance_money = res.balance_money
						this.all_money = res.all_money
					}
				}, 
			})
		},
		methods: {
			// 列表接口
			listInterface() {
				this.http({
					url: this.api.work_purchase_list,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						apply_name:this.apply_name,
						order_no:this.order_no,
						date:this.date,
						page:this.page
					},
					success: res => {
						this.page+=1
						if(res.list.length == 0) {
							uni.showToast({
								title:'没有数据',
								icon:'none',
								duration: 2000
							})
							this.list = res.list
							this.balance_money = res.balance_money
							this.all_money = res.all_money
						} else {
							this.list = res.list
							this.balance_money = res.balance_money
							this.all_money = res.all_money
						}
					}
				})
			},
			// 搜索 查询按钮点击
			search() {
				this.page = 0
				this.listInterface()
			},
			searchTime() {
				this.date = this.start_time + ' ' +'-'+ ' ' + this.end_time
				this.page = 0
				this.listInterface()
			},
			// 详情按钮点击
			detail(eve){
				console.log('eve',eve)
				uni.navigateTo({
					url:'/pages/procurement/details/details?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
		}
	}
</script>

<style lang="scss">
.total_top {
	margin-bottom: 25rpx;
	font-size: 24rpx;
	font-weight: 400;
	color: $color-9;
	.total_top_one {
		margin-right: 4rpx;
	}
	.total_top_one {
		color: $color-3;
	}
	.total_top_line {
		width: 2rpx;
		height: 28rpx;
		background: #CDCDCD;
	}
}
.btn-color {
	width: 120rpx!important;
	height: 64rpx;
	line-height: 64rpx;
	background: $color-b5;
}
.search .uni-date-editor--x .uni-date__icon-clear {
	top: -4rpx;
}
.uni-date-single {
	padding: 0!important;
	height: 64rpx;
}
.date-picker {
	width: 42%!important;
}
.uniui-calendar {
	display: none;
}
</style>
